<template>
    <div class="setup-wizard-start">
        <h1 class="title title--primary title--backgrounded">Welcome to Lightning Commerce</h1>
        <section class="section">
            <h2 class="title title--secondary">Let's get you set up!</h2>
            <p>Okay, first let's create an admin user which you'll use to login to your store's admin dashboard.</p>
            <p>This user will have full permissions access to all functionality. You'll be able to create new users with specific permissions later.</p>
            <el-card>
                <p>Please choose a password for the admin user:</p>
                <el-input v-model="password" placeholder="password" style="margin-bottom: 20px; width: 300px; display: block;" @input="getStrength()" />
                <el-progress :percentage="strength*20" :status="status" style="width: 250px;"></el-progress>
                <p v-if="strength < 3" style="margin: 5px 0;"><small>Password not strong enough</small></p>
                <el-input v-model="passwordConfirm" placeholder="confirm password" style="margin: 20px 0; width: 300px; display: block;" />
                <el-alert
                v-if="error"
                type="error"
                :title="error"
                :closable="false"
                style="margin-bottom: 20px !important; padding: 10px !important; font-size: 0.9em;">
                </el-alert>
                <el-button type="primary" @click="next()" :loading="loading">Continue</el-button>
            </el-card>
        </section>
    </div>
</template>

<script>
    const validate = require("zxcvbn");

    export default {
        name: "setup-wizard-start",
        data() {
            return {
                password: "",
                passwordConfirm: "",
                strength: 0,
                strengthMessage: "",
                status: null,
                error: null,
                loading: false
            }
        },
        methods: {
            getStrength() {
                const result = validate(this.password)
                this.strength = result.score + 1
                switch(result.score) {
                    case 0, 1:
                        this.status = "exception";
                        if(this.error) this.error = "Password not strong enough."
                        break;
                    case 2:
                        this.status = "warning";
                        if(this.error) this.error = null
                        break;
                    case 3, 4:
                        this.status = "success";
                        if(this.error) this.error = null
                        break;
                }
            },
            next() {
                this.error = null
                if(this.strength < 3) {
                    this.error = "Password not strong enough."
                } else if(this.passwordConfirm !== this.password) {
                    this.error = "Whoops, those passwords don't match."
                } else {
                    this.loading = true;
                    this.$axios.post("/setup/users", {
                        username: "admin",
                        isRoot: true,
                        password: this.password
                    })
                    .then((res) => {
                        this.loading = false;
                        this.$router.push("/setup/wizard/password-reset")
                    })
                    .catch((err) => {
                        this.$message.error("Error creating account")
                        this.$message.error(err.message)
                    })
                }
            }
        }
    }
</script>